<script>
  export let files = [{ name: "erdddddddd.js" }, { name: "errr" }];
  export let selectedFileIndex = 0;

  function onFileSelected(index) {
    // rename a file
    if (selectedFileIndex === index) {
      const newName = prompt("Please enter a file name", files[index].name);
      if (
        files.find(
          (file, fileIndex) =>
            file.name === newName && fileIndex !== selectedFileIndex
        )
      ) {
        alert("This name already exists");
        return;
      }
      if (newName.length > 0) files[index].name = newName;
      return;
    }
    // select a file
    selectedFileIndex = index;
    console.log("FILE", selectedFileIndex);
  }

  function onAddFile() {
    files = [
      ...files,
      {
        jsonIndex: 0, //set to this when selecting this file
        boilerIndex: 1,
        filePath: "/",
        name: "another.js",
        data: "vblah blah" //storage of the right dif
      }
    ];
  }
  function onRemoveFile() {
    const newData = [...files];
    newData.splice(selectedFileIndex, 1);
    selectedFileIndex = 0;
    files = [...newData];
  }
</script>

<style>
  div {
    width: 100%;
    margin-top: 4px;
    height: 42vh;
    max-width: 300px;
    overflow: auto;
  }
  .button-group {
    background-color: #46464631;
    color: rgb(128, 128, 128);
    cursor: pointer;
    width: 100%;
    display: block;
    text-align: left;
    border: none;
  }
  .active-button {
    background-color: rgb(53, 53, 53);
    color: yellow;
  }
  .save-button {
    width: 90%;
    margin-left: 8px;
  }
</style>

<span>

  <span>
    Files
    <button on:click={onAddFile} class="circled">+</button>
    <button on:click={onRemoveFile} class="circled">-</button>
  </span>

  <div>
    {#each files as file, index}
      <button
        on:click={() => onFileSelected(index)}
        value={index}
        title={'path:' + file.filePath}
        class={index === selectedFileIndex ? 'button-group  active-button' : 'button-group '}>
        {file.name}
      </button>
    {/each}

  </div>
  <button class="save-button">Save files</button>

</span>
