<script>
  import Cluster from "./layout/Cluster";
  export let onSelect; //needed
  export let curIndex = 0;
  export let data;
  export let value;
  export let title = "";
  function onChangeName(newValue) {
    data[curIndex].name = newValue.target.value;
  }

  // if (value) onSelect(value, curIndex, data);
  function selectJsonBlock(index) {
    value = data[index].data;
    onSelect && onSelect(value, index, data);
  }

  function onAddBlock() {
    const newId = data.length;
    data = [
      ...data,
      {
        id: newId,
        name: "New" + newId,
        data: Array.isArray(value)
          ? [{ value: newId, text: "string", yesorno: true }]
          : "new" //todo-fix for strings
      }
    ];

    selectJsonBlock(newId);
  }
  function onAddToArray() {
    const newData = [...data];
    newData[curIndex].data.push(
      newData[curIndex].data.length
        ? data[curIndex].data[0]
        : {
            value: 0,
            text: "string",
            yesorno: true
          }
    );
    data = [...newData];
    selectJsonBlock(curIndex);
  }

  function onRemoveBlock() {
    const newData = [...data];
    newData.splice(curIndex, 1);
    data = [...newData];
    console.log("after deleted:", data, curIndex);
    selectJsonBlock(0);
  }

  function onSelectBlock(newValue) {
    selectJsonBlock(newValue.target.value);
  }
  // console.log(typeof data[curIndex].data, typeof value);
</script>

<style>
  .input-name {
    width: 80px;
  }
</style>

<Cluster justify="left" space="4px">
  <div>
    <slot name="start" />
    <div style="pad-left: 22px">{title}</div>
    <select on:change={onSelectBlock} bind:value={curIndex}>
      {#each data as entry, index}
        <option value={index} title={entry.name}>{index}</option>
      {/each}
    </select>
    <input
      on:input={onChangeName}
      value={data[curIndex].name}
      class="input-name" />
    <button on:click={onAddBlock} title="Add new Data block" class="circled">
      +
    </button>
    {#if Array.isArray(data[curIndex].data)}
      <button
        on:click={onAddToArray}
        class="circled"
        title="Add item to array (copy from first item)">
        ++
      </button>
    {/if}
    <button
      on:click={onRemoveBlock}
      title="Remove current Data block"
      class="circled">
      -
    </button>
    <slot />
  </div>
</Cluster>
