<script>
  // import * as DarkModeToggle from "dark-mode-toggle"; //todo- fix

  import { onMount } from "svelte";
  export let name;

  import { dataStore, outputStore, fileIndex } from "./store";
  import DataBlock from "./DataBlock";
  import Dropdown from "./Dropdown";

  import Ace from "./Ace";
  import Dif from "./Dif";
  import JsonEditor from "./JsonEdit";
  import Sidebar from "./layout/Sidebar";
  import Box from "./layout/Box";
  import Grid from "./layout/Grid";
  import Cluster from "./layout/Cluster";
  import Stack from "./layout/Stack.svelte";

  import FileList from "./FileList";

  function downloadJSON() {
    var text = JSON.stringify({ ...$dataStore });
    var a = document.createElement("a");
    var file = new Blob([text], { type: "application/json" });
    a.href = URL.createObjectURL(file);
    a.download = document.getElementById("fileName").value + ".stache";
    a.click();
    a.remove();
  }

  function onOpenFile(event) {
    var uploadedFile = event.target.files[0];
    var filename = uploadedFile.name;
    var extension = filename.replace(/^.*\./, "");
    if (extension != "stache") {
      alert("not supported");
      return;
    } else {
      var readFile = new FileReader();
      readFile.onload = function(e) {
        var contents = e.target.result;
        var json = JSON.parse(contents);
        $dataStore = json;
        $fileIndex = 0; //todo fix
        // fileIndex.update(n => 0);
      };
      readFile.readAsText(uploadedFile);
    }
  }
</script>

