  <Sidebar contentMin="70%">
    <div slot="sidebar" class="sidebar">
      <!-- <Stack> -->
      <JsonEditor
        bind:json={$dataStore.jsonData}
        bind:curJsonIndex={$dataStore.fileData[$fileIndex].jsonIndex}>
        <Dropdown name="Menu">
          ---------------------------------------
          <!-- webapp -->
          <input
            type="file"
