<script>
  import { onMount } from "svelte";
  import AceDiff from "ace-diff";
  import "ace-diff/dist/ace-diff-dark.min.css";
  // import "ace-diff/dist/ace-diff.min.css";
  import { fileIndex } from "./store";

  export let differ = null;
  export let left;
  // export let right;
  export let selectedFileIndex;
  export let files;

  function editorInit(editor) {
    console.log("EDITOR", editor);
    editor.getSession().setMode("ace/mode/javascript");
    editor.setTheme("ace/theme/monokai");
    editor.setOptions({
      enableBasicAutocompletion: true,
      enableLiveAutocompletion: true
    });
    // editor.detail.setValue(
  }

  onMount(async () => {
    differ = new AceDiff({
      element: ".acediff",
      left: {
        content: left //+ "empty"
      },
      right: {
        content: files[selectedFileIndex].data //"empty"
      },
      diffGranularity: "specific"
    });
    require("brace/theme/monokai");
    require("brace/theme/clouds_midnight");
    console.log(differ.getEditors().left.getSession());
    differ
      .getEditors()
      .right.getSession()
      .on("change", () => {
        console.log(differ);
        files[selectedFileIndex].data = differ
          .getEditors()
          .right.getSession()
          .getValue();
      });

    // differ
    //   .getEditors()
    //   .left.getSession()
    //   .on("change", () => {
    //     differ.diffs.forEach(element => {
    //       element.rightEndLine -= 1;
    //     });
    //   });

    editorInit(differ.getEditors().left);
    editorInit(differ.getEditors().right);
  });

  $: {
    if (differ) {
      differ
        .getEditors()
        .left.getSession()
        .setValue(left);
      // differ
      //   .getEditors()
      //   .right.getSession()
      //   .setValue(right);
    }
  }

  fileIndex.subscribe(index => {
    selectedFileIndex = index;
    if (differ) {
      differ
        .getEditors()
        .right.getSession()
        .setValue(files[index].data);
    }
  });
</script>

<style>
  .acediff {
    position: relative;
    height: 50vh;
  }
  .acediff.gutter {
  }
</style>

<div class="parent">
  <div class="acediff" />
</div>
