<script>
  import { onMount } from "svelte";
  import { setContext } from "svelte";

  import { fileIndex } from "./store";
  import "jsoneditor/dist/img/jsoneditor-icons.svg";
  import "jsoneditor/dist/jsoneditor.min.css";
  import DataBlock from "./DataBlock";

  export let json;
  export let curJsonIndex = 0;

  let editor;
  const jsonEditorOptions = {
    onChange: function() {
      json[curJsonIndex].data = editor.get();
    }
  };
  onMount(async function() {
    const container = document.getElementById("jsoneditor");
    if (!container) return;
    const JSONEditor = require("jsoneditor/dist/jsoneditor.min");
    editor = new JSONEditor(container, jsonEditorOptions);
    onSelectJsonBlock(json[0].data);
  });

  function onSelectJsonBlock(data) {
    editor.set(json[curJsonIndex].data);
    editor.expandAll();
  }
  fileIndex.subscribe(index => {
    if (editor && json[index]) {
      console.log();
      editor.set(json[index].data);
      editor.expandAll();
    }
  });
</script>

<style>
  .json-editor {
    width: 380px;
    height: 95vh;
    overflow: auto;
    background-color: rgb(131, 131, 131);
  }
  .json-editor > div > jsoneditor-search {
    width: 40px;
  }
  @media only screen and (max-width: 600px) {
    .json-editor {
      max-height: 20vh;
    }
  }
</style>

<DataBlock
  title="json:"
  onSelect={onSelectJsonBlock}
  bind:data={json}
  bind:curIndex={curJsonIndex}>
  <div slot="start">
    <slot />
  </div>
</DataBlock>
<div class="pad-top">
  <div id="jsoneditor" class=" json-editor" />
</div>
