<script>
  // import beautify from "js-beautify";
  import AceEditor from "svelte-ace-editor";
  // import { Tabs, Tab, TabList, TabPanel } from "svelte-tabs";
  import DataBlock from "./DataBlock";

  export let code;
  export let curIndex = 0;
  $: value = code[curIndex].data;

  let editor;
  let tabs;

  if (process.browser) {
    require("brace");
    require("brace/ext/language_tools");
    require("brace/ext/beautify");
    require("brace/mode/javascript");
    require("brace/keybinding/vim");
    require("brace/theme/monokai");
    require("brace/theme/clouds_midnight");
    // selectCodeBlock(curIndex);
  }

  function editorInit(editor) {
    editor.detail.getSession().setMode("ace/mode/javascript");
    editor.detail.setTheme("ace/theme/monokai");
    editor.detail.setOptions({
      enableBasicAutocompletion: true,
      enableLiveAutocompletion: true
    });
    editor = editor.detail;
  }
  function onEditorChange(newValue) {
    console.log(curIndex, newValue);
    code[curIndex].data = newValue.detail;
  }
</script>

<div>
  <DataBlock title="mustache:" bind:data={code} bind:curIndex>
    <slot />
  </DataBlock>
  <div class="pad-top">
    <AceEditor
      bind:value
      theme="monokai"
      lang="javascript"
      width="100%"
      height="45vh"
      on:init={editorInit}
      on:input={onEditorChange} />
  </div>

</div>
