import { writable, derived } from 'svelte/store';
import Handlebars from "handlebars"

const exampleBoiler = `
{{#items}}

{{name}}() {
  {{stuff}}
}

{{/items}}
`;

// console.log(Handlebars)
export const fileIndex = writable(0);
export const dataStore = writable({
  fileData: [
    {
      jsonIndex: 1, //set to this when selecting this file
      boilerIndex: 0,
      filePath: '/',
      name: 'myFile.js',
      data: 'code blah blah', //storage of the right dif
    },
    {
      jsonIndex: 0, //set to this when selecting this file
      boilerIndex: 1,
      filePath: '/',
      name: 'another.js',
      data: 'vblah blah', //storage of the right dif
    },
  ],
  codeData: [
    {
      id: 0,
      name: 'fufu',
      data: exampleBoiler,
    },
    { id: 1, name: 'keke', data: 'ddf' },
  ],
  jsonData: [
    { id: 0, name: 'list1', data: {items: [{ name: 'myFunction', stuff: 123 }]} },
    { id: 1, name: 'list2', data: {items: [{ jimbo: 'ddf', rats: false }]} },
  ],
  outputs: [{ id: 0, name: 'jsextension.js', data: 'out' }],
});

export const outputStore = derived(
  [dataStore, fileIndex],
  ([$data, $fileIndex]) => {
    const codeIndex = $data.fileData[$fileIndex].boilerIndex;
    const curCode = $data.codeData[codeIndex].data;
    const jsonIndex = $data.fileData[$fileIndex].jsonIndex;
    const curJson = $data.jsonData[jsonIndex] && $data.jsonData[jsonIndex].data;
    
    const template = Handlebars.compile(curCode);
    const outputDataPreview = template(curJson); //todo:make items the name of the object

    return outputDataPreview; //$data.jsonData[0].name + 'err';
  }
);
